<template>
	<view class="content">
		<!-- 标签组 -->
		<view class="tab">
			<u-tabs-swiper ref="uTabs" :list="tabName" :current="currentTab" @change="tabsChange" :is-scroll="false" :show-bar="false"
			 font-size="45" gutter="10" class="swiperTab"></u-tabs-swiper>
		</view>
		<!-- 标签组结束 -->
		<!-- 滑动组件 -->
		<swiper :current="currentTab" @change="swiperChange" class="swiper">
			<!-- 第一个滑动组件页面 单次计时器 -->
			<swiper-item class="swiperPage">
				<!-- 圆形进度条 -->
				<u-circle-progress active-color="#2979ff" :percent="singleTimerProgress" width="400" class="singleTimer" duration="0" border-width="20">
					<view class="u-progress-content">
						<view class="u-progress-dot"></view>
						<!-- 如果单次计时器的设定时间为0,则显示"设置计时器" -->
						<view class='u-progress-info' v-if="singleTimerSettingTime == 0">
							<u-button type="primary" plain shape="circle" class="singleTimerSettingButton" @click="showSingleTimerPicker = true">设置计时器</u-button>
							<u-picker v-model="showSingleTimerPicker" mode="time" :params="singleTimerPickerParams" default-time="00:00:00" @confirm="confirmSingleTimePicker"></u-picker>
						</view>
						<!-- 如果存在设定时间且计时器为暂停状态，则显示暂停时的时间 -->
						<view v-else-if="isSingleTimerPaused" class="singleTimerShowNumber">
							{{singleTimerShowString}}
							<!-- 倒计时组件，不显示 -->
						</view>
						<!-- 如果单次计时器有设定的时间,且计时器为非暂停状态，则显示倒计时组件 -->
						<view class='u-progress-info singleTimerShowNumber' v-else>
							{{singleTimerShowString}}
							<u-count-down :timestamp="singleTimerComponentTime" @change="singleTimerChange" @end="singleTimerEnd" v-show="false"></u-count-down>
						</view>
					</view>
				</u-circle-progress>
				<!-- 圆形进度条结束 -->
				<u-popup v-model="showSingleTimerSettingLayer" mode="center" width="500rpx" height="200px">
					<view class="singleTimerSettingLayer">
						<view class="singleTimerSettingTitle">
							音量
						</view>
						<u-slider v-model="timerVolume" class="singleTimerVolumeSetting"></u-slider>
						<view class="singleTimerSettingTitle">
							铃声
						</view>
						<u-select v-model="showTimerRingtoneSetting" :list="timerRingtone"></u-select>
						<u-button @click="showTimerRingtoneSetting = true">打开</u-button>
					</view>
				</u-popup>
				<!-- 单次计时器已保存计时器按钮组 -->
				<!-- <view class="singleTimerList bottomButtonGruop">
					<view class="button">
						<view>10</view>
						<view>分钟</view>
					</view>
					<view class="button">
						<u-icon name="plus" size="50"></u-icon>
					</view>
				</view> -->
				<!-- 已保存计时器按钮组 结束 -->
				<!-- 底部按钮组开始 -->
				<view class="bottomButtonGruop">
					<view class="button">
						<u-icon name="list-dot" size="50"></u-icon>
					</view>
					<!-- 开始暂停按钮，如果暂停状态则显示开始按钮，如果开始状态则显示暂停按钮 -->
					<view class="button" v-if="isSingleTimerPaused" @click="startSingleTimer">
						<u-icon name="play-right-fill" size="50"></u-icon>
					</view>
					<view class="button" v-else @click="pauseSingleTimer">
						<u-icon name="pause" size="50"></u-icon>
					</view>
					<view class="button" @click="showSingleTimerSettingLayer = true">
						<u-icon name="setting-fill" size="50" @></u-icon>
					</view>
				</view>
				<!-- 底部按钮组结束 -->
			</swiper-item>
			<!-- 单次计时器结束 -->
			<!-- 循环计时器 -->
			<swiper-item class="swiperPage">
				<view class="loopTimer">
					
					<!-- <view class="loopTimerCount">
						<text class="left">
							循环次数
						</text>
						<view class="right">
							<u-number-box v-model="loopTimerCount"></u-number-box>
						</view>
					</view> -->
					
					<view class="loopTimerBox" v-for="(item,index) in currentSwipper">
						<view class="loopTimerTitle">
							{{item.title}}
						</view>
						<view class="loopTimerTime">
							{{item.time}}
						</view>
						<!-- <view class="loopTimerAction">
							<u-button type="primary" shape="circle" size="mini">
								<u-icon name="edit-pen" size="28"></u-icon>
							</u-button>
							<u-button type="warning" shape="circle">警告按钮</u-button>	
							<view class="actionButton">
								<u-icon name="edit-pen" size="28"></u-icon>
							</view>
						</view> -->
						
					</view>
					
					<view class="button plusLoopTimerButton">
						<u-icon name="plus" size="50"></u-icon>
					</view>
				</view>
				<view class="bottomButtonGruop">
					<!-- <view class="button">
						<u-icon name="list-dot" size="50"></u-icon>
						
					</view> -->
					<view class="button">
						<u-icon name="play-right-fill" size="50"></u-icon>
					</view>
					<view class="button">
						<u-icon name="list-dot" size="50"></u-icon>
					</view>
				</view>
			</swiper-item>
			<!-- 循环计时器结束 -->
		</swiper>
		<!-- 滑动组件结束 -->
	</view>
</template>



<script>	
	// document.title="土豆计时器"
	
	export default {
		data() {
			return {
				// 单次计时器相关变量开始
				
				// 标签相关变量
				// 标签页标题
				tabName: [{
						name: '单次'
					},
					{
						name: '循环'
					}
				],
				// 当前标签
				currentTab: 0,
				
				// 控制开关变量
				// 单次计时器的时间选择器是否显示
				showSingleTimerPicker:false,
				// 单次计时器是否暂停
				isSingleTimerPaused:true,
				// 是否显示计数器的设置弹出层
				showSingleTimerSettingLayer:false,
				// 是否显示铃声选择
				showTimerRingtoneSetting:false,
				
				
				// 时间相关变量
				// 单次计时器组件参数时间
				singleTimerComponentTime:0,
				// 单次计时器设定时间
				singleTimerSettingTime:0,
				//单次计时器暂停时间
				singleTimerPauseTime:0,
				// 单次计时器当前剩余时间
				singleTimerSurplusTime:0,
				
				// 时间显示字符串相关变量
				// 单次计时器显示时间字符串
				singleTimerShowString:'',
				
				// 单次计时器进度条相关变量
				// 单次计时器暂停时进度条进度
				singleTimerPauseProgress:0,
				// 单次计时器进度条进度
				singleTimerProgress:0,				
				
				// 单次计时器选择器参数
				singleTimerPickerParams:{
					hour: true,
					minute: true,
					second: true
				},
				
				// 计时器设置相关变量
				// 计时器音量
				timerVolume:60,
				// 计时器铃声
				timerRingtone:[
					1,
					2,
					3
				],
				
				
				// 单次计时器相关变量结束
				
				// 循环计时器相关变量开始
				// 循环计时器是否开启
				isLoopTimerStarting: false,
				
				// 循环计时器次数
				loopTimerCount:0,
				// 循环计时器内容
				currentSwipper:[
					{
						title:"计时器1",
						time:"12:13:14"
					},
					{
						title:"计时器2",
						time:"12:13:14"
					},
					{
						title:"计时器3",
						time:"12:13:14"
					}
				]
			}
		},
		onLoad() {
			
		},
		methods: {
			// 标签和滑块内容相关函数
			// 标签切换时触发
			tabsChange(index) {
				this.currentTab = index
			},
			// 滑块容器切换时触发
			swiperChange(e) {
				this.currentTab = e.detail.current
			},
			
			// 工具函数
			// 时:分:秒 形式的时间转换为秒数
			timeToSeconds(h,m,s){
				return parseInt(h)*3600 + parseInt(m)*60 + parseInt(s)
			},
			// 秒数转换为 时:分:秒 形式的字符串
			secondsToString(seconds){			
				let h = (seconds/3600) < 10 ? '0' + parseInt(seconds/3600) : parseInt(seconds/3600)
				let m = parseInt(seconds/60) >= 60 ? parseInt(seconds/60) % 60 : parseInt(seconds/60)
				m = m<10 ? '0'+m : m 
				let s = (seconds%60) < 10 ? '0' + parseInt(seconds%60) : parseInt(seconds%60)
				
				return h + ' : ' + m + ' : ' + s
			},
			
			// 单次计时器相关函数开始
			
			// 单次计时器时间选择器确认后触发
			confirmSingleTimePicker(e){
				this.singleTimerSettingTime = this.timeToSeconds(e.hour,e.minute,e.second)
				this.singleTimerPauseTime = this.singleTimerSettingTime
				this.singleTimerShowString = e.hour + ' : ' + e.minute + ' : ' + e.second
			},
			
			// 开始单次计时器
			startSingleTimer(){

				if(this.singleTimerSettingTime != 0){
					this.singleTimerProgress = this.singleTimerPauseProgress
					this.singleTimerComponentTime = this.singleTimerPauseTime
					this.isSingleTimerPaused = false
				}

			},
			// 暂停单次计时器
			pauseSingleTimer(){

				this.isSingleTimerPaused = true
				this.singleTimerShowString = this.secondsToString(this.singleTimerPauseTime)
				this.singleTimerPauseProgress = this.singleTimerProgress
			},
			
			// 单次计时器事件触发的函数
			// 单次计时器时间改变时触发
			singleTimerChange(surplusTime){
				this.singleTimerProgress = ( 1 - (surplusTime / this.singleTimerSettingTime)) * 100
				this.singleTimerPauseTime = surplusTime
				// this.singleTimerPauseProgress = this.singleTimerProgress
				this.singleTimerShowString = this.secondsToString(surplusTime)				
			},
			// 单次计时器结束时触发
			singleTimerEnd(){
				this.singleTimerPauseTime = this.singleTimerSettingTime
				this.isSingleTimerPaused = true
				this.singleTimerProgress = 0
				this.singleTimerShowString = this.secondsToString(this.singleTimerSettingTime)
			}
			
			// 单次计时器相关函数结束
		},
		computed:{
			
		}
	}
</script>

<style lang="scss">
	page,
	.content {
		height: 100%;
		// padding-top: 20rpx;
		box-sizing: border-box;

	}

	.tab {
		height: 10%;

		.swiperTab {
			padding-top: 50rpx;
		}
	}

	.swiper {
		height: 90%;
	}

	.swiperPage {
		display: flex;
		flex-direction: column;
		align-items: center;
		box-sizing: border-box;

		.singleTimer {
			padding-top: 200rpx;

			.singleTimerSettingButton {
				font-weight: bold;
				
			}
			
			.singleTimerShowNumber{
				font-size: 55rpx;
				font-weight: bold;
			}
		}
		
		

		.loopTimer {
			padding-top: 50rpx;
			display: flex;
			flex-direction: column;
			align-items: center;
			.loopTimerBox{
				display: flex;
				// flex-direction: column;
				align-items: center;
				justify-content: space-around;
				width: 650rpx;
				height: 100rpx;
				box-shadow: 0 7rpx 10rpx rgba(0, 0, 0, 0.19);
			}
			.plusLoopTimerButton{
				margin-top: 70rpx;
			}
		}


	}



	.bottomButtonGruop {
		width: 100%;
		bottom: 10%;
		position: absolute;
		display: flex;
		justify-content: space-around;
	}
	
	.singleTimerList{
		bottom: 25%;
		.button{
			background-color: #F1F1F1;
			display: flex;
			flex-direction: column;
			font-size: 20rpx;
			color: black;
			font-weight: 500;
		}
	}
	
	.button {
		border: 1px solid #F1F1F1;
		width: 90rpx;
		height: 90rpx;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		color: rgb(34, 131, 246);
		box-shadow: 0 7rpx 10rpx rgba(0, 0, 0, 0.19);
	}
	
	.singleTimerSettingLayer{
		display: flex;
		flex-direction: column;
		align-items: center;
		.singleTimerSettingTitle{
			padding: 50rpx;
			font-size: 40rpx;
			font-weight: 500;
		}
		.singleTimerVolumeSetting{
			width: 400rpx;
			// padding-top: 30rpx;
		}
	}
	
</style>
